
.radio-group.is-score {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.radio-group.is-score .qt-item {
    display: inline-block;
    width: 33%;
    text-align: center;
}
.radio-group.is-score .qt-item .check {
      width: 72px;
      height: 72px;
      background-color: #FAFAFA;
      box-shadow: 0px 4px 8px #d5caca;
      border: none;
}
.radio-group.is-score .qt-item .check:before {
        display: none;
}
.radio-group.is-score .qt-item .control-label {
      padding-left: 0;
      position: absolute;
      left: 0;
      top: 28px;
      width: 100%;
      font-size: 16px;
      font-weight: 700;
}
.radio-group.is-score .qt-item .radio input[type=radio]:checked + .check {
      color: #fff;
      box-shadow: 0px 4px 8px #d5caca;
      background-color: #fa4d60;
}
.radio-group.is-score .qt-item .radio input[type=radio]:checked + .check + .control-label {
      color: #fff;
}
.radio-group.is-image .qt-item, .radio-group.is-sex .qt-item {
  display: inline-block;
  width: 33%;
  height: 140px;
  text-align: center;
  position: relative;
}
.radio-group.is-image .qt-item .check, .radio-group.is-sex .qt-item .check {
    width: 100px;
    height: 100px;
    border-radius: 0;
    background-size: auto 100%;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    border: none;
}
.radio-group.is-image .qt-item .check:before, .radio-group.is-sex .qt-item .check:before {
      display: none;
}
.radio-group.is-image .qt-item .control-label, .radio-group.is-sex .qt-item .control-label {
    padding-left: 0;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    color: #fa4d60;
    border: 1px solid #fa4d60;
    border-radius: 3px;
    padding: 0 8px;
    line-height: 18px;
    height: 20px;
    overflow: hidden;
    top: 110px;
    font-size: 12px;
}
.radio-group.is-image .qt-item .radio input[type=radio]:checked + .check, .radio-group.is-sex .qt-item .radio input[type=radio]:checked + .check {
    box-shadow: none;
}
.radio-group.is-image .qt-item .radio input[type=radio]:checked + .check + .control-label, .radio-group.is-sex .qt-item .radio input[type=radio]:checked + .check + .control-label {
    color: #fff;
    background-color: #fa4d60;
}
.radio-group.is-sex .qt-item {
  width: 50%;
}
.radio {
  outline: none;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: relative;
}
.radio + .radio {
  margin-left: .5em;
}
.radio input[type=radio] {
  display: none;
}
.radio input[type=radio] + .check {
  position: relative;
  width: 1.25em;
  height: 1.25em;
  border: 2px solid #7a7a7a;
  background: none;
  border-radius: 50%;
}
.radio input[type=radio] + .check:before {
  content: "";
  position: absolute;
  border-radius: 50%;
  left: -2px;
  top: -2px;
  width: inherit;
  height: inherit;
  background: #fa4d60;
  -webkit-transform: scale(0);
  transform: scale(0);
}
.radio input[type=radio]:checked + .check {
  border-color: #fa4d60;
}
.radio input[type=radio]:checked + .check:before {
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
}
.radio .control-label {
  padding-left: .5em;
}
.radio[disabled] {
  opacity: .5;
}
.radio:hover input[type=radio] + .check {
  border-color: #fa4d60;
}
.radio:focus input[type=radio] + .check {
  box-shadow: 0px 4px 8px #d5caca;
}
.radio:focus input[type=radio]:checked + .check {
  box-shadow: 0px 4px 8px #d5caca;
}



